.m-hero {
  background-color: var(--gray-5);
}
.m-hero__wrapper {
  display: grid;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 30px;
  padding-bottom: 30px;
}
.m-hero__text {
  padding-right: 15px;
  padding-left: 15px;
}
.m-hero__heading {
  font-size: 2.125em;
  font-weight: 600;
  letter-spacing: inherit;
  line-height: 1.25;
  text-transform: inherit;
  margin-bottom: 0.4411764706em;
}
p + .m-hero__heading, ul + .m-hero__heading, ol + .m-hero__heading, dl + .m-hero__heading, figure + .m-hero__heading, img + .m-hero__heading, table + .m-hero__heading, blockquote + .m-hero__heading {
  margin-top: 1.7647058824em;
}
.m-hero__subhead {
  font-size: 22px;
  line-height: 1.25;
}
.m-hero__image-wrapper {
  box-sizing: border-box;
  overflow: hidden;
}
.m-hero__image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  display: none;
}
@supports (display: grid) {
  .m-hero__image {
    display: block;
  }
}
.m-hero--knockout {
  background-color: var(--gray);
  color: var(--white);
}
.m-hero--overlay .m-hero__wrapper {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.m-hero--jumbo, .m-hero--50-50 {
  background-color: var(--white);
}
.m-hero--jumbo .m-hero__subhead, .m-hero--50-50 .m-hero__subhead {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.375em;
  font-weight: normal;
  letter-spacing: inherit;
  line-height: 1.25;
  text-transform: inherit;
}
@media only all and (max-width: 37.5em) {
